.footer {
  background-color: whitesmoke;
  width: 100%;
  color: black;
  padding: 1rem;
  line-height: 2.5rem;
  
  
  &__copyright {
    text-align: center;
  }

  &__address {
    line-height: 2rem;
  }

  & li {
    cursor: pointer;
    &:hover {
      transform: scale(1.01);
      font-weight: bold;
    }
  }
}

.footer-info {
  width: 100%;
  margin: 0 auto;
  padding: 1rem;
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;

  &__col {
    flex: 1 1 auto;
    max-width: 100%;
    min-width: 15rem;
    margin-block: 1rem;
    &--wide {
      min-width: 20rem;
    }

    &--address {
      background-color: white;
      border-radius: 4px;
      padding: 2rem;
      font-size: 2rem;
      @include flexCenter(column);
      gap: 1rem;
    }
    &--offset {
      justify-self: center;
      text-align: center;
      width: 100%;
    }
  }
}

.socialLinks {
  margin-bottom: 1rem;

  &__link {
    margin-left: 2rem;
  }
  
}